* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  overflow-y: scroll;
}

p {
  margin: 0;
}

.header {
  z-index: 5;
  width: 100%;
  background-color: #0ff;
  position: fixed;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 150px;
}

.images-info-wrapper {
  justify-content: space-between;
  display: flex;
}

.images-info {
  margin-right: 20px;
  display: inline-block;
}

.search-form {
  width: 100%;
  max-width: 350px;
}

.search-form input, .search-form button {
  float: left;
  border: 1px solid gray;
  padding: 5px;
  font-size: 16px;
}

.search-form input {
  width: 90%;
  background: #f1f1f1;
  border-radius: 5px 0 0 5px;
}

.search-form input:focus, .search-form input:focus ~ button {
  outline: 1px solid #0b7dda;
}

.search-form button {
  width: 10%;
  color: #fff;
  cursor: pointer;
  background: #2196f3;
  border-left: none;
  border-radius: 0 5px 5px 0;
}

.search-form button:hover {
  background: #0b7dda;
}

.search-form:after {
  content: "";
  clear: both;
  display: table;
}

.switch {
  width: 150px;
  height: 22px;
  font-size: 16px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 110px;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(18px);
}

.slider.round {
  border-radius: 22px;
}

.slider.round:before {
  border-radius: 50%;
}

.gallery-container {
  padding-top: 100px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.card-wrapper {
  transition: scale .4s;
  position: relative;
}

.card-info {
  width: 100%;
  color: rgba(255, 255, 0, .7);
  opacity: 0;
  background-color: rgba(33, 150, 243, .667);
  justify-content: space-around;
  padding: 5px;
  transition: opacity .8s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.likes .fa {
  color: brown;
}

.card-image {
  width: 280px;
  height: 160px;
  object-fit: cover;
  border-radius: 6px;
  display: block;
}

.card-wrapper:hover {
  z-index: 2;
  scale: 1.1;
}

.card-wrapper:hover > .card-info {
  cursor: pointer;
  opacity: 1;
  border-radius: 6px 6px 0 0;
}

.card-wrapper:hover .card-image {
  box-shadow: 2px 2px 10px 3px rgba(0, 0, 0, .3);
}

.card-plus {
  text-align: center;
}

.card-image-load {
  width: 280px;
  opacity: .3;
  cursor: pointer;
  background-color: #ccc;
  border-radius: 6px;
  margin-top: 10px;
  padding: 6px;
  font-size: 16px;
}

.scrollup-button {
  z-index: 99;
  color: rgba(33, 150, 243, .733);
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

.scrollup-button:hover {
  color: #0b7dda;
  scale: 1.1;
}

.pagination {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 30px;
}

.pagination-button {
  width: 45px;
  height: 25px;
  background-color: #f1f1f1;
  border: 1px solid #0b7dda;
  transition: background-color .3s;
}

.pagination-button:hover:not(.pagination-button-current):not(.pagination-button-more), .pagination-button:focus:not(.pagination-button-current):not(.pagination-button-more) {
  cursor: pointer;
  background-color: rgba(0, 255, 255, .4);
}

.pagination-button-current {
  background-color: #0ff;
}

.pagination-button:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.pagination-button:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.modal {
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .4);
  padding-top: 100px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-wrapper {
  width: 80%;
  background-color: #fefefe;
  border: 1px solid #888;
  border-radius: 5px;
  margin: auto;
  position: relative;
}

.modal-close {
  color: #000;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  top: 5px;
  right: 15px;
}

.modal-close:hover, .modal-close:focus {
  color: #9acd32;
  cursor: pointer;
  text-decoration: none;
}

.modal-content {
  display: flex;
}

.modal-large-image {
  width: 100%;
  border-radius: 5px;
}

.modal-image {
  object-fit: cover;
  width: 60%;
  padding: 20px;
}

.modal-info {
  padding: 20px;
}

.modal-form {
  flex-direction: column;
  padding-top: 5px;
  display: flex;
}

.modal-text-label {
  display: inline;
}

.modal-text {
  width: 100%;
  height: 100%;
  resize: none;
  margin-bottom: 10px;
}

.modal-submit {
  margin-top: 10px;
}

